body {
  background: linear-gradient(
    to right,
    rgba(36, 31, 31, 1) 0%,
    rgba(36, 31, 31, 1) 32%
  );
  color: #f8f8f8;
  box-sizing: border-box;
  background-attachment: fixed;
}

.contact-sub-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

