/* Photo */

/* The name and effects */
.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.nameContainer-contact {
  font-size: 4rem;
  display: inline-flex;
  gap: 1rem;
}

.firstName-contact {
  display: flex;
}

.firstLetter:hover {
  color: #bda2a2;
  transition: color 0.3s;
  cursor: default;
  animation: shake 0.5s;
}

.lastName-contact {
  color: #bda2a2;
  display: flex;
  font-weight: lighter;
}

.lastLetter:hover {
  color: #f8f8f8;
  transition: color 0.3s;
  cursor: default;
  animation: shake 1s;
}

/*The social medi links*/
.socialLinks {
  background-color: #241f1f;
  box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.5);
  width: auto;
  padding-top: 1rem;
  padding-bottom: 1rem;
  border-radius: 10%;
  position: relative;
  bottom: 15px;
}

.social-links-list {
  font-family: serif;
  list-style: none;
  font-size: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: left;
  margin-top: 0px;
  margin-bottom: 0px;
}

.social-links-list > li {
  margin-top: 20px;
  margin-bottom: 20px;
}

.social-links-list > li:hover {
  /*We need to do this because in the style.css already transition is applied on li*/
  transform: none;
  cursor: default;
}

.social-links-list a {
  text-decoration: none;
  color: #f8f8f8;
}

.social-links-list > li > a:hover {
  transition: none;
  color: #a45959;
  transition: color 0.3s;
}

.social-links-list p {
  font-size: 1.5rem;
  color: #bda2a2;
}

/* Lets Chat Button */
.whatsApp {
  margin-right: 11.5rem;
  margin-left: auto;
}

.btn-contact {
  height: 3rem;
  width: 12rem;
  border: none;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  font-size: 1.5rem;
  font-weight: bold;
  padding-top: 0px;
  margin-top: 20px;
  align-items: center;
  color: #a45959;
  background-color: #bda2a2;
}

.btn-contact:hover {
  background-color: #a45959;
  color: #bda2a2;
  transition: color 0.3s;
  cursor: pointer;
}

@media (max-width: 700px) {
  body {
    background: linear-gradient(to right, #bda2a2 50%, #241f1f 50%);
    background-attachment: fixed;
    color: #f8f8f8;
    overflow-x: hidden;
    margin: 0;
    padding: 0;
  }
  .h {
    display: none;
  }

  .mainBg {
    display: flex;
    flex-direction: column;
  }

  .nameContainer {
    margin-left: 3rem;
  }

  .contact-sub-container {
    display: grid;
    grid-template-rows: 0.5fr 1fr;
    grid-template-columns: 1fr;
  }

  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .content > h1 {
    margin-top: 0px;
    margin-bottom: 0px;
  }

  .nameContainer-contact {
    font-size: 3rem;
  }

  .firstName-contact {
    margin-left: 1rem;
  }

  p {
    color: #f8f8f8;
    margin: 0;
    z-index: 1;
    padding-bottom: 1rem;
  }

  .socialLinks {
    width: auto;
  }

  .social-links-list {
    padding-left: 1rem;
    word-break: break-all;
    font-size: 1.5rem;
  }

  .social-links-list > li {
    margin-bottom: 0;
  }

  .whatsApp {
    display: flex;
    align-self: flex-end;
    margin: 0;
  }

  .btn-contact {
    width: 9rem;
  }
}
